import {FormEvent, ReactElement, useEffect, useRef, useState} from "react";
import Modal, {ModalProps} from "../components/Modal";
import {PartialCourse, useAutocomplete} from "../CourseAPI";
import {Select} from "antd";

export interface AddModalProps
{
    handleSubmit: (crn: string) => void
    handleClose: () => void
}

export default function AddModal({handleSubmit, handleClose}: AddModalProps) : ReactElement
{
    const [crn, setCrn] = useState<string>();
    const {isLoading, data} = useAutocomplete(parseInt(crn ?? ""), 10);

    const onSubmit = (ev: FormEvent<HTMLFormElement>) => {
        ev.preventDefault();
        handleSubmit(crn ?? "");
    };

    return <Modal title="Enter CRN" onSubmit={() => handleSubmit(crn ?? "")} onClose={handleClose}>
        <form onSubmit={onSubmit}>
            <Select showSearch placeholder="Select a Course" value={crn} onChange={setCrn} onSearch={setCrn}
                    options={data?.map(x => ({label: `${x.crn} - ${x.courseTitle}`, value: x.crn}))} className="w-100"
                    loading={isLoading && !!crn}/>
        </form>
    </Modal>;
}