import {AnimatePresence, motion, MotionProps, Reorder, useDragControls, useMotionValue} from "framer-motion";
import {Key, MutableRefObject, ReactElement, ReactNode, useState} from "react";
import ReorderIcon from "./ReorderIcon";
import {useRaisedShadow} from "../hooks/userRaisedShadow";

export interface DragItemProps<V>
{
    value: V,
    children: ReactNode
}

export default function DragItem<V>({value, children}: DragItemProps<V>) : ReactElement
{
    const controls = useDragControls();
    const y = useMotionValue(0);
    const boxShadow = useRaisedShadow(y);

    return <Reorder.Item style={{boxShadow, y}} value={value} dragListener={false} dragControls={controls} className="my-2">
        <div className="d-flex rounded-1 p-1" style={{backgroundColor: "var(--light)"}}>
            <div className="justify-content-center d-flex flex-column mx-2 w-100 position-relative" style={{cursor: "pointer"}}>
                <motion.div layout>
                    {children}
                </motion.div>
            </div>
            <ReorderIcon dragControls={controls} className="ms-auto mb-0 align-self-center"/>
        </div>
    </Reorder.Item>;
}