import {ReactElement} from "react";
import {Course, GetDayName, SwrResult} from "../CourseAPI";
import {LayoutGroup, motion} from "framer-motion";
import Spinner from "./Spinner";
import {useLocalTime} from "../hooks/useLocalTime";

export interface DetailedPillProps
{
    result: SwrResult<Course>,
    onDelete: () => void
}

export default function DetailedPill({result, onDelete} : DetailedPillProps) : ReactElement
{
    const {data: course, isLoading, isValidating, error} = result;
    const toLocalTime = useLocalTime();

    return <div className="border-top border-2 border-dark pt-2">
        <div className="row">
            <div className="col">
                <div className="fw-bold">Instructors</div>
                <div>{course?.instructors.join(",")}</div>
            </div>
            <div className="col">
                <div className="fw-bold">Capacity</div>
                <div>{course?.enrolled}/{course?.capacity}</div>
            </div>
        </div>
        <div className="row mt-2">
            <div className="col">
                <div className="fw-bold">Schedule</div>
                <div>{course?.times.map(x => <div>
                    {GetDayName(x.dayOfWeek)} : {toLocalTime(x.startTime)}-{toLocalTime(x.endTime)}
                </div>)}</div>
            </div>
        </div>
        <div className="row mt-2 px-3 pb-1">
            <button className="btn btn-danger text-white" onClick={onDelete}>
                <i className="bi-trash"></i>
            </button>
        </div>
    </div>;
}