import {ReactElement} from "react";
import {DragControls, motion} from "framer-motion";

export interface ReorderIconProps
{
    dragControls: DragControls,
    className?: string
}

export default function ReorderIcon({dragControls, className}: ReorderIconProps) : ReactElement
{
    return <motion.h2 layout className={className} style={{cursor: "grab"}}>
        <i className="bi bi-grip-vertical" onPointerDown={e => dragControls.start(e)}></i>
    </motion.h2>;
}