import {MutableRefObject, ReactElement, useState} from "react";
import DragItem from "./DragItem";
import {useCourse} from "../CourseAPI";
import Spinner from "./Spinner";
import {AnimatePresence, motion} from "framer-motion";
import DetailedPill from "./DetailedPill";
import BasicPill from "./BasicPill";

export interface CoursePillProps
{
    crn: number,
    onDelete: () => void
}

export default function CousePill({crn, onDelete} : CoursePillProps) : ReactElement
{
    const result = useCourse(crn);
    const [isOpen, setIsOpen] = useState(false);

    return <motion.div initial={{x: "-25%", opacity: 0}} animate={{x: 0, opacity: 1}} exit={{x: "25%", opacity: 0}}
        transition={{duration: 0.2}}>
        <DragItem value={crn}>
            <motion.div onClick={() => setIsOpen(!isOpen)}>
                <BasicPill result={result}/>
                <AnimatePresence>
                    {isOpen && <motion.div key={`detail-${crn}`} animate={{opacity: 1, height: "fit-content"}}
                                           initial={{opacity: 0, height: 0}} exit={{opacity:0, height: 0}}
                                           style={{overflow: "hidden"}} layout>
                        <DetailedPill result={result} onDelete={onDelete}/>
                    </motion.div>}
                </AnimatePresence>
            </motion.div>
        </DragItem>
    </motion.div>;
}