import { motion } from "framer-motion";
import {ReactElement} from "react";
import Spinner from "./Spinner";
import {Course, SwrResult} from "../CourseAPI";

export interface BasicPillProps
{
    result: SwrResult<Course>
}

export default function BasicPill({result}: BasicPillProps) : ReactElement
{
    const {data: course, isLoading, isValidating, error} = result;

    return <motion.div className="d-flex flex-row py-3 position-relative" layout>
        <span className="fw-bold border-end border-dark px-1 border-2 align-self-center h-100">{course?.crn}</span>
        <span className="mx-3">{course?.courseTitle}</span>
        {isLoading && !error && <Spinner/> }
        {!!error && <span className="text-danger">
                    {error}
                </span>}
    </motion.div>;
}